
import {
    HashRouter as Router,
    Switch,
} from "react-router-dom"

import { AuthProvider, PrivateRoute } from "@meddbriefer/mdb-auth";
import { useFirebaseProject, FirebaseContext } from "@meddbriefer/mdb-firebase"

import "bootstrap/dist/css/bootstrap.min.css";

import { environment } from "./environments/environment";

import LogList from "./routes/LogList"
import AnalyzeDebriefing from "./components/AnalyzeDebriefing";
import DebLog from "./components/DebLog"
import DeleteCommentLog from "./components/DeleteCommentLog";
import DeleteObsLogs from "./components/DeleteObsLogs";

import UnauthedLayout from "./components/UnauthedLayout"


const App = (props) => {
    const {auth, db } = useFirebaseProject(environment.firebaseProject)
    return (
      <FirebaseContext.Provider
        value={{
            auth,
            db
        }}
      >
        <AuthProvider>
          <Router>
            <Switch>
              <PrivateRoute
                exact
                path="/logs/:logID"
                authFlowLayout={UnauthedLayout}
                component={AnalyzeDebriefing}
              />
              <PrivateRoute
                exact
                path="/observer/delete/:logID"
                authFlowLayout={UnauthedLayout}
                component={DeleteObsLogs}
              />
              <PrivateRoute
                exact
                path="/comment/display/:logID"
                authFlowLayout={UnauthedLayout}
                component={DebLog}
              />
              <PrivateRoute
                exact
                path="/comment/delete/:logID"
                authFlowLayout={UnauthedLayout}
                component={DeleteCommentLog}
              />
              {/*default (catchall) route*/}
              <PrivateRoute
                match
                path="/"
                authFlowLayout={UnauthedLayout}
                component={LogList}
              />
            </Switch>
          </Router>
        </AuthProvider>
      </FirebaseContext.Provider>
    )
}


export default App;